.button {
  display: block;
  width: 280px;
  padding: 10px 0;
  border: none;

  color: #ffffff;
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  text-align: center;
  text-transform: uppercase;

  background-color: #68b738;
}

.button--font {
  padding: 11px 0;

  font-size: 14px;
  line-height: 18px;
}

.button:hover {
  background-color: #5eaa2f;
}

.button:active span {
  opacity: 0.3;
}

.button--color {
  color: #444444;

  background-color: #f2f2f2;
}

.button--color:hover {
  background-color: #ebebeb;
}
